<template>
    <div class="el-combo-table el-select"> 
        <el-popover
            ref="selectPopover"
            placement="bottom-start"
            trigger="click"
            v-model="visible"
            :visible-arrow="false">
            <el-table-x
                ref="selectTableX"
                :default-load="defaultLoad"
                :pageable="pageable"
                :data-url="dataUrl"
                :base-params="baseParams"
                :checkbox="multiple"
                :row-index="rowIndex"
                :table-columns="tableColumns"
                :table-filters="tableFilters"
                :height="tableHeight"
                :max-height="tableMaxHeight"
                :stripe="stripe"
                :border="border"
                :fit="fit"
                :show-header="showHeader"
                :highlight-current-row="highlightCurrentRow"
                :hide-column="hideColumn"
                :empty-text="emptyText"
                :default-sort="defaultSort"
                :request-type="requestType"
                :async="async"
                :json-data="jsonData"
                :json-total="jsonTotal"
                :page-key="pageKey"
                :size-key="sizeKey"
                :condition-key="conditionKey"
                :current-row-key='currentRowKey' 
                :row-key='dataId'
                @selection-change="selectionsChange"
                @row-click = "rowClickFn"
                @row-dblclick = "rowClickFn">
            </el-table-x>
        </el-popover>
        <el-input
            :placeholder="placeholder"
            v-popover:selectPopover
            v-model="selectedLabel"
            :readonly="true"
            :icon="iconClass"
            :disabled="disabled"
            :on-icon-click="handleIconClick"
            @mouseenter.native="inputHovering = true"
            @mouseleave.native="inputHovering = false">
        </el-input>
    </div>
</template>

<script>
import { addClass, removeClass, hasClass } from 'element-ui/src/utils/dom';
export default {
  name: 'ElComboTable',
  props: {
    value: {
      required: true
    },
    dataId: {
      type: String,
      default: 'id',
      required: true
    },
    dataLabel: {
      type: String,
      default: 'label',
      required: true
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    clearable: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: false
    },
    /* 表格组件参数*/
    defaultLoad: {
      type: Boolean,
      default: true
    },
    pageable: {
      type: Boolean,
      default: true
    },
    dataUrl: String,
    baseParams: Object,
    rowIndex: Boolean,
    tableColumns: Array,
    hideColumn: Boolean,
    tableFilters: Object,
    tableHeight: [String, Number],
    tableMaxHeight: [String, Number],
    border: Boolean,
    fit: {
      type: Boolean,
      default: true
    },
    stripe: {
      type: Boolean,
      default: true
    },
    showHeader: Boolean,
    highlightCurrentRow: {
      type: Boolean,
      default: true
    },
    emptyText: String,
    defaultSort: Object,
    requestType: String,
    async: Boolean,
    jsonData: String,
    jsonTotal: String,
    pageKey: String,
    sizeKey: String,
    conditionKey: String
  },
  data: function() {
    var selValue = this.multiple ? [] : '';
    selValue = this.value ? this.value : selValue;
    return {
      inputHovering: false,
      tempSelVal: selValue,
      selectedLabel: '',
      visible: false,
      tableSelections: [],
      currentRowKey: ''
    };
  },
  watch: {
    tableSelections: function(selections) {
      if (selections.length > 0) {
        var tempArryVal = [];
        var tempArryLabel = [];
        for (var i = 0, length = selections.length; i < length; i++) {
          tempArryVal.push(selections[i][this.dataId]);
          tempArryLabel.push(selections[i][this.dataLabel]);
        }
        this.tempSelVal = this.multiple ? tempArryVal : tempArryVal[0];
        this.selectedLabel = this.multiple
          ? tempArryLabel.join(',')
          : tempArryLabel[0];
      } else {
        var blankVal = this.multiple ? [] : '';
        this.tempSelVal = blankVal;
        this.selectedLabel = '';
      }
    },
    value: function(val) {
      if (this.tempSelVal !== val) {
        this.tempSelVal = val;
        // this.setSelected();
      }
    },
    tempSelVal: function(val) {
      this.$emit('input', val);
      this.$emit('change', val);
    },
    visible: function(val) {
      if (!val) {
        this.handleIconHide();
      } else {
        this.handleIconShow();
      }
      this.$emit('visible-change', val);
    }
  },
  computed: {
    iconClass: function() {
      var criteria =
        this.clearable &&
        !this.disabled &&
        this.inputHovering &&
        this.value !== undefined &&
        (this.multiple ? this.value.length > 0 : this.value !== '');
      return criteria ? 'circle-close is-show-close' : 'caret-top';
    }
  },
  methods: {
    selectionsChange: function(selections) {
      this.tableSelections = selections;
    },
    rowClickFn: function(row, event, column) {
      if (!this.multiple) {
        this.tableSelections = this.$refs.selectTableX.selections;
        if (this.tableSelections.length > 0) {
          this.visible = false;
        }
      }
    },
    setSelected: function() {
      var tableData = this.$refs.selectTableX.data;
      var vals = this.tempSelVal;
      var dataId = this.dataId;
      var rows = [];
      if (this.multiple) {
        if (vals.length > 0) {
          for (var j = 0, length2 = vals.length; j < length2; j++) {
            for (var i = 0, length = tableData.length; i < length; i++) {
              if (vals[j] === tableData[i][dataId]) {
                rows.push(tableData[i]);
                break;
              }
            }
          }
        }
      } else {
        if (vals) {
          for (var n = 0, len = tableData.length; n < len; n++) {
            if (vals === tableData[n][dataId]) {
              rows.push(tableData[n]);
              break;
            }
          }
        }
      }
      this.$nextTick(function() {
        if (this.multiple) {
          this.$refs.selectTableX.clearSelection();
          for (var i = 0, length = rows.length; i < length; i++) {
            this.$refs.selectTableX.toggleRowSelection(rows[i]);
          }
        } else {
          this.currentRowKey = vals;
        }
        this.setSelectedLabel(rows);
      });
    },
    setSelectedLabel: function(selections) {
      this.$nextTick(function() {
        if (selections.length > 0) {
          var tempArryLabel = [];
          for (var i = 0, length = selections.length; i < length; i++) {
            tempArryLabel.push(selections[i][this.dataLabel]);
          }
          this.selectedLabel = this.multiple
            ? tempArryLabel.join(',')
            : tempArryLabel[0];
        } else {
          this.selectedLabel = '';
        }
      });
    },
    handleIconClick: function(event) {
      event.stopPropagation();
      if (this.iconClass.indexOf('circle-close') > -1) {
        this.clear();
      } else {
        this.toggleMenu(event);
      }
    },
    toggleMenu: function() {
      if (!this.disabled) {
        this.visible = !this.visible;
      }
    },
    handleIconHide: function() {
      var icon = this.$el.querySelector('.el-input__icon');
      if (icon) {
        removeClass(icon, 'is-reverse');
      }
    },
    handleIconShow: function() {
      var icon = this.$el.querySelector('.el-input__icon');
      if (icon && !hasClass(icon, 'el-icon-circle-close')) {
        addClass(icon, 'is-reverse');
      }
    },
    getSelectedObjs: function() {
      var _this = this;
      var resultObj;
      if (_this.multiple) {
        // 多选
        resultObj = _this.tableSelections;
      } else {
        // 单选
        resultObj = _this.tableSelections[0];
      }
      return resultObj;
    },
    clear: function() {
      if (this.multiple) {
        this.$refs.selectTableX.clearSelection();
      } else {
        this.currentRowKey = '';
        this.tempSelVal = '';
        this.selectedLabel = '';
      }
      this.visible = false;
      this.$emit('clear');
    }
  },
  mounted: function() {
    //    if(this.tempSelVal || this.tempSelVal.length>0){
    //         this.setSelected();
    //     }
  }
};
</script>




