<template>
  <div class="el-combo-tree el-select">
            <el-popover
                ref="selectPopover"
                placement="bottom-start"
                trigger="click"
                v-model="visible"
                :visible-arrow="false">
                <el-tree-x
                    ref="selectTreeX"
                    @check-change="checkChangeFn"
                    @node-click="nodeClickFn"
                    :empty-text="emptyText"
                    :current-node-key="currentNodeKey"
                    :show-checkbox="multiple"
                    :node-key="dataId"
                    :indent="indent"
                    :async="async"
                    :root-visible="rootVisible"
                    :data-url="dataUrl"
                    :data-params="dataParams"
                    :json-data="jsonData"
                    :expand-on-click-node="false"
                    :request-type="requestType"
                    :data-id="dataId"
                    :data-label="dataLabel"
                    :data-pid="dataPid"
                    :data-root="dataRoot"
                    :lazy="lazy"
                    :local-data="localData"
                    :highlight-current="highlightCurrent">
                </el-tree-x>
        </el-popover>
        <el-input
            :placeholder="placeholder"
            v-popover:selectPopover
            v-model="selectedLabel"
            :readonly="true"
            :icon="iconClass"
            :disabled="disabled"
            :on-icon-click="handleIconClick"
            @mouseenter.native="inputHovering = true"
            @mouseleave.native="inputHovering = false">
        </el-input>
    </div>
</template>

<script>
import { addClass, removeClass, hasClass } from 'element-ui/src/utils/dom';
export default {
  name: 'ElComboTree',
  props: {
    value: {
      required: true
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    clearable: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: false
    },
    emptyText: String,
    requestType: String,
    lazy: Boolean,
    dataUrl: String,
    dataId: String,
    dataLabel: String,
    dataPid: String,
    dataRoot: [String, Object],
    async: Boolean,
    rootVisible: {
      type: Boolean,
      default: true
    },
    highlightCurrent: {
      type: Boolean,
      default: true
    },
    localData: Array,
    indent: Number,
    dataParams: Object,
    jsonData: String
  },
  data: function() {
    var selValue = this.multiple ? [] : '';
    selValue = this.value ? this.value : selValue;
    return {
      inputHovering: false,
      tempSelVal: selValue,
      selectedLabel: '',
      currentNodeKey: '',
      visible: false
    };
  },
  watch: {
    value: function(val) {
      if (this.tempSelVal !== val) {
        this.tempSelVal = val;
      }
      this.setSelected();
    },
    tempSelVal: function(val) {
      this.$emit('input', val);
      this.$emit('change', val);
    },
    visible: function(val) {
      if (!val) {
        this.handleIconHide();
      } else {
        this.handleIconShow();
      }
      this.$emit('visible-change', val);
    }
  },
  computed: {
    iconClass: function() {
      var criteria =
        this.clearable &&
        !this.disabled &&
        this.inputHovering &&
        this.value !== undefined &&
        (this.multiple ? this.value.length > 0 : this.value !== '');
      return criteria ? 'circle-close is-show-close' : 'caret-top';
    }
  },
  methods: {
    setSelected: function() {
      if (this.multiple) {
        this.$refs.selectTreeX.setCheckedKeys(this.tempSelVal);
      } else {
        this.currentNodeKey = this.tempSelVal;
      }
      this.setSelectedLabel();
    },
    checkChangeFn: function(nodeData, checked, indeterminate) {
      // 底层控件选择事件触发
      if (this.multiple) {
        this.tempSelVal = this.$refs.selectTreeX.getCheckedKeys(true);
        this.setSelectedLabel();
      }
    },
    nodeClickFn: function(nodeData, node, self) {
      if (!this.multiple && node.isLeaf) {
        this.tempSelVal = nodeData[this.dataId];
        this.selectedLabel = nodeData[this.dataLabel];
        this.visible = false;
      }
    },
    handleIconClick: function(event) {
      event.stopPropagation();
      if (this.iconClass.indexOf('circle-close') > -1) {
        this.clear();
      } else {
        this.toggleMenu(event);
      }
    },
    toggleMenu: function() {
      if (!this.disabled) {
        this.visible = !this.visible;
      }
    },
    getSelectedObjs: function() {
      var _this = this;
      var resultObj;
      if (_this.multiple) {
        // 多选
        resultObj = _this.$refs.selectTreeX.getCheckedNodes(true);
      } else {
        // 单选
        resultObj = {};
        var treeData = _this.$refs.selectTreeX.orginalData;
        for (var i = 0; i < treeData.length; i++) {
          if (treeData[i][_this.dataId] === _this.tempSelVal) {
            resultObj = treeData[i];
            break;
          }
        }
      }
      return resultObj;
    },
    clear: function() {
      if (this.multiple) {
        this.tempSelVal = [];
      } else {
        this.tempSelVal = '';
      }
      this.selectedLabel = '';
      this.visible = false;
      this.$emit('clear');
    },
    setSelectedLabel: function() {
      this.$nextTick(function() {
        // 填充输入框内容
        if (this.multiple) {
          // 多选
          var checkedNodes = this.$refs.selectTreeX.getCheckedNodes(true);
          if (checkedNodes.length > 0) {
            var _this = this;
            this.selectedLabel = checkedNodes
              .reduce(function(arr, cur) {
                arr.push(cur[_this.dataLabel]);
                return arr;
              }, [])
              .join(',');
          } else {
            this.selectedLabel = '';
          }
        } else {
          if (this.tempSelVal === '') {
            this.selectedLabel = '';
            return;
          }
          // 单选：填充值前一定要保证树结构已经加载完成，即orginalData有值
          var allNode = this.$refs.selectTreeX.orginalData;
          for (var i = 0; i < allNode.length; i++) {
            if (allNode[i][this.dataId] === this.tempSelVal) {
              this.selectedLabel = allNode[i][this.dataLabel];
              break;
            }
          }
        }
      });
    },
    handleIconHide: function() {
      var icon = this.$el.querySelector('.el-input__icon');
      if (icon) {
        removeClass(icon, 'is-reverse');
      }
    },
    handleIconShow: function() {
      var icon = this.$el.querySelector('.el-input__icon');
      if (icon && !hasClass(icon, 'el-icon-circle-close')) {
        addClass(icon, 'is-reverse');
      }
    }
  },
  mounted: function() {
    this.setSelected();
  }
};
</script>




